/* Login.css */

.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f9fafb;
    padding: 20px;
  }
  
  .login-card {
    background-color: #ffffff;
    width: 320px;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .login-card h1 {
    font-size: 2em;
    color: #333;
    margin-bottom: 20px;
  }
  
  /* Form group styling */
  .form-group {
    margin-bottom: 15px;
    text-align: left;
  }
  
  .form-group label {
    display: block;
    font-size: 1em;
    color: #555;
    margin-bottom: 5px;
  }
  
  .form-input {
    width: 100%;
    padding: 10px;
    font-size: 1em;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-sizing: border-box;
  }
  
  /* Login button styling */
  .login-button {
    width: 100%;
    padding: 10px;
    font-size: 1em;
    color: white;
    background-color: #4caf50;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
    transition: background-color 0.3s;
  }
  
  .login-button:hover {
    background-color: #388e3c;
  }
  
  /* Styled return button link */
  .return-button {
    display: inline-block;
    margin-top: 20px;
    padding: 8px 15px;
    font-size: 0.9em;
    color: #4caf50;
    background-color: #f0f4f8;
    border-radius: 5px;
    text-decoration: none;
    transition: background-color 0.3s;
  }
  
  .return-button:hover {
    background-color: #e0e7eb;
  }
  
  /* Add to each css, prevents horizontal scrolling, confines to screen */
body, html {
    overflow-x: hidden; /* Disable horizontal scrolling */
    width: 100vw;
    margin: 0;
    padding: 0;
  }
  
  *,
  *::before,
  *::after {
    box-sizing: border-box; /* Include padding/border in element width */
  }
  
  /* Limit main container to viewport width */
  .home-container, .debits-container, .credits-container {
    max-width: 100vw; /* Confines content within viewport width */
    overflow-x: hidden; /* Prevents horizontal scroll within each container */
  }
