/* UserProfile.css */

.profile-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f9fafb;
    padding: 20px;
  }
  
  .profile-card {
    background-color: #ffffff;
    width: 300px;
    padding: 20px;
    border-radius: 12px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .profile-avatar {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    margin-bottom: 20px;
  }
  
  .profile-title {
    font-size: 2em;
    color: #333;
    margin-bottom: 15px;
  }
  
  .profile-detail {
    font-size: 1.1em;
    color: #555;
    margin-bottom: 10px;
    display: flex;
    justify-content: space-between;
  }
  
  .detail-label {
    font-weight: bold;
    color: #4caf50;
  }
  
  .return-button {
    display: inline-block;
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 1em;
    color: white;
    background-color: #4caf50; 
    border-radius: 6px;
    text-decoration: none;
    transition: background-color 0.3s;
  }
  
  .return-button:hover {
    background-color: #388e3c;
  }
  
  /* Add to each css, prevents horizontal scrolling, confines to screen */
body, html {
    overflow-x: hidden; /* Disable horizontal scrolling */
    width: 100vw; 
    margin: 0;
    padding: 0;
  }
  
  *,
  *::before,
  *::after {
    box-sizing: border-box; /* Include padding/border in element width */
  }
  
  /* Limit main container to viewport width */
  .home-container, .debits-container, .credits-container {
    max-width: 100vw; /* Confines content within viewport width */
    overflow-x: hidden; /* Prevents horizontal scroll within each container */
  }
