/* Debits.css */

/* Main container */
.debits-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
    width: 100%;
  }
  
  /* Header, balance, red for debits */
  .debits-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    background-color: #e53935;
    color: white;
    padding: 15px 40px;
    padding-right: 30px;
    border-radius: 8px;
    margin-bottom: 20px;
  }
  
  /* Account balance styling */
  .balance-display {
    display: flex;
    align-items: center;
    font-size: 1.2em;
    font-weight: bold;
    color: #f9fafb;
    background-color: rgba(0, 0, 0, 0.2); /* Box overlay */
    padding: 8px 12px;
    border-radius: 20px;
  }
  
  .balance-icon {
    margin-right: 8px;
    font-size: 1.5em;
  }
  
  /* Table container */
  .debits-table-container {
    width: 100%;
    max-width: 600px;
  }
  
  /* Table styling */
  .debits-table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .debits-table th, .debits-table td {
    padding: 10px;
    border: 1px solid #e1e4e8;
    text-align: left;
  }
  
  .debits-table th {
    background-color: #e53935;
    color: white;
    font-weight: bold;
  }
  
  /* Add Debit form styling */
  .add-debit-container {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .add-debit-input {
    width: 100%;
    padding: 8px;
    margin: 8px 0;
    font-size: 16px;
    border-radius: 4px;
    border: 1px solid #ddd;
  }
  
  .add-debit-button {
    background-color: #e53935;
    color: white;
    font-size: 16px;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  .add-debit-button:hover {
    background-color: #d32f2f; /* Darker red on hover */
  }

   /* Add to each css, prevents horizontal scrolling, confines to screen */
body, html {
    overflow-x: hidden; /* Disable horizontal scrolling */
    width: 100vw;
    margin: 0;
    padding: 0;
  }
  
  *,
  *::before,
  *::after {
    box-sizing: border-box; /* Include padding/border in element width */
  }
  
  /* Limit main container to viewport width */
  .home-container, .debits-container, .credits-container {
    max-width: 100vw; /* Confines content within viewport width */
    overflow-x: hidden; /* Prevents horizontal scroll within each container */
  }
