/* Home.css */

/* Custom font for the header */
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap');

.home-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  width: 100%;
}

/* Full-width colored stripe for the header with embedded account balance */
.home-header {
  width: 100vw;
  background-color: #4caf50;
  text-align: center;
  padding: 15px 0;
  margin: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 40px;
  box-sizing: border-box;
}

.home-header h1 {
  font-family: 'Roboto', sans-serif;
  font-size: 2.5em;
  color: white; 
  margin: 0;
}

/* Account balance styling */
.balance-display {
  display: flex;
  align-items: center;
  font-size: 1.2em;
  font-weight: bold;
  color: #f9fafb;
  background-color: #388e3c;
  padding: 8px 12px;
  border-radius: 20px;
}

.balance-icon {
  margin-right: 8px;
  font-size: 1.5em;
}

/* 2x2 layout */
.card-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  width: 100%;
  max-width: 600px;
  margin-top: 40px; /* Space below header */
}

/* Card styling */
.card {
  background-color: #f9fafb;
  border-radius: 8px;
  padding: 15px;
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  text-decoration: none;
  color: #333;
  transition: transform 0.3s, box-shadow 0.3s;
}

.card img {
  border-radius: 50%;
  margin-bottom: 10px;
  width: 100px;
  height: 100px;
}

.card h2 {
  font-size: 1.5em;
  color: #333;
  margin-bottom: 10px;
}

.card p {
  font-size: 1em;
  color: #666;
}

/* Hover effect for cards */
.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

/* Add to each css, prevents horizontal scrolling, confines to screen */
body, html {
    overflow-x: hidden; /* Disable horizontal scrolling */
    width: 100vw; 
    margin: 0;
    padding: 0;
  }
  
  *,
  *::before,
  *::after {
    box-sizing: border-box; /* Include padding/border in element width */
  }
  
  /* Limit main container to viewport width */
  .home-container, .debits-container, .credits-container {
    max-width: 100vw; /* Confines content within viewport width */
    overflow-x: hidden; /* Prevents horizontal scroll within each container */
  }
